.topBar{
    

    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    
    margin: 0px 100px;
    
}

.otherBtns{
    display: flex;
    align-items: center;
    
}
.btn--nav{
    margin: 0 20px;
}
.name--logo{
    width: 139px;
    height: 22px;
    padding-top: 0;
    background-image:url(../../Photos/Namelogo.png);
    background-position: 50% 100%;
    background-size: 139px;
    transition: background-position .3s ease;
    background-repeat: no-repeat;
}
.name--logo:hover{
    background-position: 50% 10%;
}