
.btn--nav{
padding: 5px 20px;
border:#00FFC2 solid 2px ;

font-weight: 700;


background-size: auto;
text-transform: uppercase;
text-decoration: none;
outline: none;

cursor: pointer;

background-color:#132326;
background-size:     cover;  
color: #fff;
transition: all 0.3s ease-out;


}
.btn:active, .bth:focus{
    outline: 0;
    outline-style: none;
}

.btn--nav:hover{
    box-shadow: inset 0 2.5em 0 0 #00FFC2;
    color: #132326;
    
}

.btn--paragraph{
    border:#00FFC2 solid 2px ;
    
    font-weight: 700;

    
    height: 50px;
    padding: 0px 40px;
    margin:0 0 0 10px;

    background-size: auto;
    text-transform: uppercase;
    text-decoration: none;
    outline: none;
    
    cursor: pointer;
    
    background-color:#132326;
    background-size:     cover;  
    color: #fff;
    transition: all 0.3s ease-out;
    
    
    
}

.btn--paragraph:hover{
    box-shadow: inset 13em 0 0 0 #00FFC2;
    color: #132326;
}