.App{
    
    margin: 0;
    padding: 0;
    height: 99vh;
    width: 100%;

    display: grid;
    grid-template-columns:auto ;
    grid-template-rows: 100px auto ;
    
}
.app{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.main--png{


}

.social--media{
    width:100% ;
    height: 70px;

    display: flex;
    justify-content:space-evenly;
    align-items: center;
}
.home--page{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    /* margin: 0 0 0 400px; */
    margin: 100px 0;
    justify-self: center;
}
.paragraph{
    width:500px;
    margin-left: 80px;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.social--icons{
        background-repeat: no-repeat;
    background-size: cover;
    width: 50px;
    height: 50px;
    transition: background 0.5s ;
    margin: 0px 10px;
}
.fb{
    background-image: url(../Photos/fgbhovwh.png);

}
.fb:hover{
    background-image: url(../Photos//fbhovgr.png); 
}
.inst{
    background-image: url(../Photos/insthovwh.png);
}
.inst:hover{
    background-image: url(../Photos/insthovgr.png);
}
.yt{
    background-image: url(../Photos/ythovwh.png);
}
.yt:hover{
    background-image: url(../Photos/ythovgr.png);
}
.line{

    height: 4px;
    border: none;
    background-color: #00FFC2;
    width: 100%;
     
}
.hr{
    margin-top: 210px;
}
.line-animation{
    animation:  width 1s ease-out alternate;   
}

@keyframes width{
    from{
        width: 0%;
    }
    to{
        width: 100%;
    }
}